<template>
  <div class="loader" @click="clickBg">
    <div
      class="content"
      :class="{ 'content-not-wallets': loading != 'mobile' }"
    >
      <div v-if="loading == 'mobile'" class="loader-mobile">
        <div
          class="loader-mobile-item"
          v-for="(item, i) in wallets"
          :key="i"
          @click="$emit('open-wallet', item.id)"
        >
          <div>
            <img :src="`img/${item.id}.svg`" />
          </div>

          <span>
            {{ item.name }}
          </span>
        </div>
      </div>
      <p v-else-if="loading == 4">
        Authentication failed. <br />
        Please try another wallet.
      </p>
      <p v-else-if="loading == 3">
        Authorization failed.<br />Please try again.
      </p>
      <p v-else-if="loading == 1">Authorize yourself</p>
      <p v-else>Wait...</p>

      <svg
        v-if="loading !== 'mobile'"
        version="1.1"
        id="L9"
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        x="0px"
        y="0px"
        viewBox="0 0 100 100"
        enable-background="new 0 0 0 0"
        xml:space="preserve"
        width="120"
      >
        <path
          fill="rgba(252, 124, 197, 0.8)"
          d="M73,50c0-12.7-10.3-23-23-23S27,37.3,27,50 M30.9,50c0-10.5,8.5-19.1,19.1-19.1S69.1,39.5,69.1,50"
        >
          <animateTransform
            attributeName="transform"
            attributeType="XML"
            type="rotate"
            dur="1s"
            from="0 50 50"
            to="360 50 50"
            repeatCount="indefinite"
          />
        </path>
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    loading: {
      type: Boolean | Number | String,
    },
    wallets: {
      type: Array,
    },
  },
  methods: {
    clickBg(e) {
      if (e.target.classList.contains("loader") && this.loading == "mobile")
        this.$emit("close");
    },
  },
};
</script>

