<template>
  <transition name="fade">
    <div id="app">
      <transition name="fade">
        <app-loader
          v-if="loading"
          :loading="loading"
          :wallets="mobileWallets"
          @close="loading = null"
          @open-wallet="openMobileWallet"
        />
      </transition>

      <div class="bg-wrapper">
        <section class="home">
          <app-header
            :user_id="user_id"
            :default_links="default_links"
            @connect-wallet="connectWallet"
          >
            <template v-slot:nav>
              <div class="logo">
                <div class="icon">
                  <img src="img/logo.png" />
                </div>

                <div class="header-icon not-mobile">
                  <img src="img/header1.png" />
                </div>

                <div class="header-icon not-mobile">
                  <img src="img/header2.png" />
                </div>

                <div class="header-icon not-mobile">
                  <img src="img/header3.png" />
                </div>
              </div>
            </template>
          </app-header>
        </section>

        <home>
          <template v-slot>
            <claim-button
              :user_id="user_id"
              :isWrongNetwork="isWrongNetwork"
              @claim="claimAction"
            />
          </template>
        </home>

        <space />

        <about />

        <team />

        <app-footer />
      </div>
      <notifications group="foo" position="top center" />
    </div>
  </transition>
</template>

<script>
import Vue from "vue";
import { bus } from "./main";
import Notifications from "vue-notification";

import AppLoader from "@/components/Loader.vue";
import ClaimButton from "@/components/ClaimButton";

import Header from "@/components/Header";
import Footer from "@/components/Footer";
import Home from "@/components/Home";
import About from "@/components/About";
import Team from "@/components/Team";
import Space from "@/components/Space";

Vue.use(Notifications);

export default {
  name: "app",
  components: {
    AppLoader,
    ClaimButton,
    appHeader: Header,
    appFooter: Footer,
    Home,
    About,
    Team,
    Space,
  },
  created() {
    bus.$on("setNotifyLink", this.setNotifyLink);
    bus.$on("setUserAccountId", this.setUserAccountId);
    bus.$on("selectAccessMint", this.selectAccessMint);
    bus.$on("updateLoaderStatus", this.updateLoaderStatus);

    this.DOMAIN_NAME = window.location.host;
  },
  mounted() {
    // if (localStorage.getItem("autoConnect")) this.autoConnect = true;
    // if (this.autoConnect && !this.isMobile()) this.connectWallet();
  },
  beforeDestroy() {
    bus.$off("setUserAccountId", this.setUserAccountId);
    bus.$off("selectAccessMint", this.selectAccessMint);
    bus.$on("updateLoaderStatus", this.updateLoaderStatus);
    bus.$off("setNotifyLink", this.setNotifyLink);
  },
  data() {
    return {
      loading: false,
      DOMAIN_NAME: "",
      user_ETH: 0,
      user_id: null,
      autoConnect: false,
      isWrongNetwork: false,
      // mint: null,

      mobileWallets: [
        {
          id: "metamask",
          name: "MetaMask",
        },
        {
          id: "trust",
          name: "Trust Wallet",
        },
        {
          id: "coinbase",
          name: "Coinbase Wallet",
        },
      ],

      default_links: [
        {
          icon: "tel.svg",
          to: "https://twitter.com/doodles",
          id: "tel",
          name: "Twitter",
        },
        {
          icon: "dis.png",
          to: "https://discord.gg/doodles",
          id: "dis",
          name: "Discord",
        },
        {
          icon: "inst.svg",
          to: "https://www.instagram.com/welikethedoodles/",
          id: "inst",
          name: "Inst",
        },
      ],
    };
  },
  methods: {
    openMobileWallet(id) {
      if (id === "metamask") {
        window.location.href = `https://metamask.app.link/dapp/${this.DOMAIN_NAME}`;
      } else if (id === "trust") {
        window.location.href = `https://link.trustwallet.com/open_url?coin_id=60&url=https://${this.DOMAIN_NAME}`;
      } else if (id === "coinbase") {
        // window.location.href = `https://go.cb-w.com/dapp?cb_url=${escape(
        //   `https://${this.DOMAIN_NAME}`
        // )}`;

        window.location.href = `https://go.cb-w.com/dapp?cb_url=https://${this.DOMAIN_NAME}`;
      }
    },
    updateLoaderStatus(status = false) {
      this.loading = status;
    },
    selectAccessMint(val) {
      this.isWrongNetwork = !val;
    },
    isMobile() {
      let isMobile = false;
      (function (a) {
        if (
          /(android|bb\d+|meego).+mobile|avantgo|bada\/|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\browser.ink ce|xda|xiino/i.test(
            a
          ) ||
          /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(
            a.substr(0, 4)
          )
        )
          isMobile = true;
      })(navigator.userAgent || navigator.vendor || window.opera);

      return isMobile;
    },
    setUserAccountId(id) {
      if (!id) return (this.user_id = null);
      this.user_id = id;
      // localStorage.setItem("autoConnect", true);
    },
    connectWalletDesktop() {
      window.connectButtonClick();
    },
    // async connectWalletMobile() {
    //   window.location.href = `https://metamask.app.link/dapp/${this.DOMAIN_NAME}`;
    // },
    async connectWallet() {
      if (window.ethereum) {
        window.setIsMobile(this.isMobile());
        return this.connectWalletDesktop();
      } else {
        if (this.isMobile()) return (this.loading = "mobile");
        return alert("Please install metamask!");
      }
    },
    async claimAction() {
      //    this.mint = true;
      if (!this.user_id) return this.connectWallet();
      this.loading = 1;
      window.mintButtonClick();

      //  this.mint = null;
    },
    setNotifyLink(text) {
      this.$notify({
        group: "foo",
        duration: 5000,
        title: text,
        // text: `https://etherscan.io/tx/`,
      });
    },
  },
};
</script>

