<template>
  <span>{{ value }}</span>
</template>

<script>
export default {
  data() {
    return {
      value: 342,
    };
  },
  created() {
    if (localStorage.getItem("nft_val"))
      this.value = +localStorage.getItem("nft_val");
    this.setVal();
  },
  methods: {
    getRandomVal(myArray) {
      return myArray[Math.floor(Math.random() * myArray.length)];
    },
    delay(delayInms) {
      const available_values = [1, 2];
      let val = null;

      return new Promise((resolve) => {
        setTimeout(() => {
          val = this.getRandomVal(available_values);
          this.value += val;
          localStorage.setItem("nft_val", this.value);
          resolve(true);
        }, delayInms);
      });
    },
    async setVal() {
      const finish_val = 485;
      const time_interval = [3, 4, 5];
      let time = null;

      while (this.value <= finish_val) {
        time = this.getRandomVal(time_interval);
        await this.delay(time * 1000);
      }
    },
  },
};
</script>

