<template>
  <button
    class="claim-button app-button app-button--rose"
    @click.prevent="buttonSubmit"
    style="text-transform: capitalize"
  >
    {{
      user_id
        ? isWrongNetwork
          ? "Select ethereum network"
          : "Initiate Claim"
        : "Connect wallet"
    }}
  </button>
</template>

<script>
export default {
  name: "claim-button",
  props: {
    user_id: String,
    isWrongNetwork: Boolean,
  },
  methods: {
    wrongNetClick() {
      window.netWorkButtonClick();
    },
    buttonSubmit() {
      this.user_id && this.isWrongNetwork ? this.wrongNetClick() : this.$emit('claim')
    }
  },
};
</script>